	export default {
		data() {
			return {
				sizeCalcState: false,
				tabScrollTop: 0,
				currentId: 1,
 				flist: [],
				slist: [],
				tlist: [],
			}
		},
		onLoad(){
			this.loadData();
		},
		methods: {
			imgPath:function(path){
				if(path==''||path==null||path=='null'){
					return "/static/errorImage.jpg"				 
				}else if (path!=''&&path.indexOf('http')>=0) {
					return path;				 
				}
 				return this.siteHost + path;
			},
			async loadData(){
				var _this = this;
				//获取数据
				this.sendRequest({
					url : "get_product_cat",
					data : {pid : ''},
					method : "get",
					header: {'content-type' : "application/x-www-form-urlencoded"},
					success:function (res) {
						let cateList= res.data;  
						cateList.forEach(item=>{ 
							_this.flist.push(item);  //pid为父级id, 没有pid或者pid=0是一级分类
							item.children.forEach(it=>{
								_this.slist.push(it); //没有图的是2级分类
								it.children.forEach(i=>{
									_this.tlist.push(i); //3级分类
								}); 
							}) 
						}) 
						if(cateList&&cateList.length>0){
							_this.currentId=cateList[0].id;
						} 
					}
				});  
			},
			//一级分类点击
			tabtap(item){
				if(!this.sizeCalcState){
					this.calcSize();
				}
				this.currentId = item.id;
				let index = this.slist.findIndex(sitem=>sitem.parent.id === item.id);
				this.tabScrollTop = this.slist[index].top;
			},
			//右侧栏滚动
			asideScroll(e){
				if(!this.sizeCalcState){
					this.calcSize();
				}
				let scrollTop = e.detail.scrollTop;
				let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
				if(tabs.length > 0){
					this.currentId = tabs[0].parent.id;
				}
			},
			//计算右侧栏每个tab的高度等信息
			calcSize(){
				let h = 0;
				this.slist.forEach(item=>{
					let view = uni.createSelectorQuery().select("#main-" + item.id);
					view.fields({
						size: true
					}, data => {
						item.top = h;
						h += data.height;
						item.bottom = h;
					}).exec();
				})
				this.sizeCalcState = true;
			},
			navToList(sid, tid){
				uni.navigateTo({
					url: `/pages/product/list?cateId=${tid}&fid=${this.currentId}`
				})
			}
		}
	}
